import {  Grid,  List, ListItem,Typography } from "@material-ui/core";
import MenuIcon from '@material-ui/icons/Menu';
import React ,{useState} from "react";
import { Link } from "react-router-dom";
import "./Header.css";

function Header() {
  const [open, setOpen]=useState(false);

  return (
    <div className="header">
        
          <Grid className="header__Body" container align="center" justify="space-around"  >

            <Grid item justify="center" >
            <Typography  style={{color: "white" ,letterSpacing:"3px", fontWeight:"500" , margin:"10px 20px" , fontFamily: "Poppins, sans-serif"}} variant="h5" component="h5">    
             <a className="header__BodyLogo" style={{color:"white",textDecoration:"none",fontFamily: "Poppins, sans-serif"}} href="www.rajaryan.tk">Raj Aryan</a>
            </Typography>
           </Grid>

           <Grid item className="header__Link" style={{transform: open ? "translateX(0px)" : ""}}>
            <List className="header__LinkList" justify="space-around" style={{display:"flex",letterSpacing:"2px", fontWeight:"500" }} >
              <Link className="header__Links" style={{color:"white",textDecoration:"none",fontFamily: "Poppins, sans-serif" }} to="/"><ListItem onClick={()=> setOpen(!open)} style={{letterSpacing:"2px"}}>Home</ListItem></Link>
              <Link className="header__Links" style={{color:"white",textDecoration:"none",fontFamily: "Poppins, sans-serif"}} to="/about"><ListItem onClick={()=> setOpen(!open)} style={{letterSpacing:"2px"}}>About</ListItem></Link>
              <Link className="header__Links" style={{color:"white",textDecoration:"none",fontFamily: "Poppins, sans-serif"}} to="/skill"><ListItem onClick={()=> setOpen(!open)} style={{letterSpacing:"2px"}}>Skill</ListItem></Link>
              <Link className="header__Links" style={{color:"white",textDecoration:"none",fontFamily: "Poppins, sans-serif"}} to="/project"><ListItem onClick={()=> setOpen(!open)} style={{letterSpacing:"2px"}}>Project</ListItem></Link>
              <Link className="header__Links" style={{color:"white",textDecoration:"none",fontFamily: "Poppins, sans-serif"}} to="/archivements"><ListItem onClick={()=> setOpen(!open)} style={{letterSpacing:"2px"}}>Archivements</ListItem></Link>
              <Link className="header__Links" style={{color:"white",textDecoration:"none",fontFamily: "Poppins, sans-serif"}} to="/contact"><ListItem onClick={()=> setOpen(!open)} style={{letterSpacing:"2px"}}>Contact</ListItem></Link>
            </List>
           </Grid>

            <Grid item className="header__Burger" >
              <MenuIcon onClick={()=> setOpen(!open)} style={{color: "white",margin:"10px 20px" }} />
            </Grid>

           </Grid>
      
    </div>
  );
}

export default Header;
