import {  Button, Divider, Grid, Typography } from '@material-ui/core'
import React,{ useEffect } from 'react';
import "./ContactPage.css";
import Title from 'react-vanilla-tilt'
import AOS from 'aos';
import 'aos/dist/aos.css'; 
function ContactPage() {

    useEffect(() => {
        AOS.init({duration:2000});
       
      }, []);



    return (
        <div className="contactPage">

          <Grid container  align="center" style={{color:"white",height:"100vh"}}>

                 {/* About left section */}
                 
                <Grid data-aos="zoom-in"    className="contactPage__glassMorphism"  style={{ color:"white", marginTop:"120px", marginBottom:"100px", marginLeft:"100px",marginRight:"100px",padding:"20px",width:"100%"}}>
                
                <Typography className="contactPage__title" style={{padding:"2px",color:"black",fontWeight:"600" ,letterSpacing:"3px" , fontFamily: "Poppins, serif"}} variant="h4" component="h4">
                    Contact
                </Typography>
                <Divider  />
                <br/>
                <h3 justify="space-around" className="contactPage__titleEmail" style={{ color:"black",fontFamily: "Poppins, serif" ,letterSpacing:"2px",margin:"10px"}} >Email</h3>
                
                <p justify="space-around" className="contactPage__Gmail" style={{fontFamily: "Poppins, serif"}} >raj267447@gmail.com</p>
                <br/>
                <h3 justify="space-around" className="contactPage__titleMobile" style={{ color:"black",fontFamily: "Poppins, serif" ,letterSpacing:"2px",margin:"10px"}} >Mobile Number</h3>
               
                <a  justify="space-around" href="tel:+917717792439" download style={{color:"white",textDecoration:"none"}}><Button  variant="contained" color="primary" style={{fontFamily: "Poppins, sans-serif"}}>Call Now</Button></a>
                <br/>< br/>
                <h3 justify="space-around" className="contactPage__titleAddress" style={{ color:"black",fontFamily: "Poppins, serif" ,letterSpacing:"2px" ,margin:"10px"}} >Address</h3>
                
                <p justify="space-around" className="contactPage__Address" style={{fontFamily: "Poppins, serif"}} >Bengaluru , Karnataka </p>
                </Grid >
                
             </Grid>

            
        </div>
    )
}

export default ContactPage
