import React,{ useEffect } from 'react';
import "./AboutPage.css";
import { Button, Divider, Grid, Typography } from '@material-ui/core';
import AOS from 'aos';
import 'aos/dist/aos.css'; 
import Title from 'react-vanilla-tilt'

function AboutPage() {

    useEffect(() => {
        AOS.init({duration:2000});
       
      }, []);

    return (
        <div className="aboutPage">
             <Grid container  align="center" style={{color:"white",height:"100vh"}}>

                 {/* About left section */}
                 
                <Grid item  data-aos="zoom-in"  className="aboutPage__glassMorphism"  style={{ color:"white", marginTop:"120px", marginBottom:"150px" ,marginLeft:"100px",marginRight:"100px",padding:"20px",width:"100%"}}>
                
                <Typography className="aboutPage__title" style={{padding:"2px",color:"black",fontWeight:"600" ,letterSpacing:"3px" , fontFamily: "Poppins, serif"}} variant="h4" component="h4">
                    About
                </Typography>
                <Divider  />
                <br/>
                <p justify="space-around" style={{fontFamily: "Poppins, serif",letterSpacing:"2px",margin:"5px"}} >Web Developer || Python Programmer || Ethical Hacker</p>
                <br/>
                <p justify="space-around"className="aboutPage__pData" style={{fontFamily: "Poppins, serif",letterSpacing:"2px",margin:"10px"}} >I am a proactive and enthusiastic learner with efficient management skills and leadership quality seeking a position where I can efficiently contribute my skills and creative abilities for the growth of the organization while building a professional career.</p>
                <br/>
                <h3 justify="space-around" style={{ color:"black",fontFamily: "Poppins, serif" ,letterSpacing:"1px",marginBottom:"10px"}} >Download My Resume</h3>
                
                <a href="Raj_Aryan_resume.pdf"  download style={{color:"white",textDecoration:"none"}}><Button   variant="contained" color="primary" className="aboutPage__button" style={{fontFamily: "Poppins, sans-serif"}}>Click Here</Button></a>
                
                </Grid >
                
             </Grid>
        </div>
    )
}

export default AboutPage
